class ServerApi {
    setEndpoint(endPoint) {
        this.endPoint = endPoint;
        this.user = null;
        this.password = null;
    }

    async getDirectory(path) {
        if (this.user === null || this.password === null)
            return false;

        return await this.json(this.endPoint + path, {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({user: this.user, password: this.password})
        });
    }

    async json(endPoint, options) {
        let response = await fetch(endPoint, options);
        let text = await response.text();
        try {
            return JSON.parse(text);
        } catch (e) {
            console.warn("Could not parse server response", text);
            return false;
        }
    }

    async auth(user, password) {
        let result = await this.json(this.endPoint + '/auth', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({user, password})
        });
        if (result === true) {
            this.user = user;
            this.password = password;
        }
        return result;
    }
}

export default new ServerApi();