<template>
    <div class="home">
        <md-list>
            <md-list-item @click="goUp">
                <md-icon>keyboard_arrow_up</md-icon>
            </md-list-item>
            <md-list-item v-for="file in files" :key="file.path" @click="loadItem(file)">
                <md-icon v-if="file.directory">folder</md-icon>
                <md-icon v-else>cloud_download</md-icon>
                <span class="md-list-item-text">
                {{file.path}}
                </span>
            </md-list-item>
        </md-list>
    </div>
</template>

<script>
    import ServerApi from "@/js/ServerApi";

    export default {
        name: 'home',
        components: {},
        data() {
            return {
                files: []
            }
        },
        async mounted() {
            if (localStorage.getItem('auth') === null && !this.$route.path.includes('/login')) {
                await this.$router.push('/login');
            } else {
                let {user, password} = JSON.parse(localStorage.auth);
                await ServerApi.auth(user, password);
                this.initialize(this.$route.path);
            }
        },
        methods: {
            async goUp() {
                let up = this.$route.path.split('/');
                up = up.slice(0, up.length - 1);
                await this.$router.push(up.join('/'));
            },
            async initialize(path) {
                this.files = await ServerApi.getDirectory(path);
            },
            async loadItem(item) {
                if (item.directory) {
                    await this.$router.push(item.path);
                } else {
                    let a = document.createElement('a');
                    a.href = ServerApi.endPoint + '/file?token=' + item.token;
                    let fileName = item.path.split('/');
                    a.setAttribute('target', '_blank');
                    fileName = fileName[fileName.length - 1];
                    a.setAttribute('download', fileName);
                    a.click();
                }
            },
        },
        beforeDestroy() {
            clearInterval(this.interval);
        },
        watch: {
            $route(to, from) {
                console.log('to', to);
                this.initialize(this.$route.path);
            }
        }
    }
</script>

